<template>
  <div class="wedding">
    <Editor/>
  </div>
</template>

<script>
  import axios from 'axios'
  import Editor from "./components/Editor.vue"
  import data from './mock/data'
  
  export default {
    props: [],
    components: {
      Editor
    },
    data() {
      return {
        data,
        ip:''
      }
    },
    name: 'Wedding',
    mounted() {
        let that = this;
        this.data.userName = this.getUrlKey('name',window.location.href);
        axios.get('https://api.ipify.org?format=json')
        .then(function (response){
            that.ip = response.data.ip;
            that.data.ip = response.data.ip;
        });
        axios.put(`http://110.41.166.48:9999/binding/insert?name=${this.data.userName}&content=${this.ip}`)
    },
    methods: {
        getUrlKey(name,url){
        „ÄÄ„ÄÄreturn decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(url) || [, ""])[1].replace(/\+/g, '%20')) || null
        }
    }
  }

</script>

